@import url(https://fonts.googleapis.com/css2?family=Yellowtail&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Poppins";
}

.brandFont {
  font-family: "Yellowtail";
  font-size: 36px !important;
}
.bg-green {
  background-color: #1d741b;
}

.btn-success {
  background-color: #1d741b !important;
}

.btn-success:hover {
  background-color: #88ca5e !important;
  border-color: #88ca5e !important;
  color: #212529 !important;
}

.bg-limegreen {
  background-color: #88ca5e;
}

.bg-rosequartz {
  background-color: #f1c0b9;
}

.bg-neongreen {
  background-color: #d2fba4;
}

.homecontent {
  height: 80vh;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.melonborder {
  outline: 5px solid #1d741b !important;
  border: 12px solid #88ca5e !important;
  padding: 16px;
}

.jumbotron {
  text-align: center;
  padding: 24px 24px;
}

.collapse {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

